<template>
	<div id="my" >
		<div id="userInfo">
			<div class="userBox" @click='showMyInfo=true'>
				<img src="http://img2.imgtn.bdimg.com/it/u=3363472252,3522967664&fm=26&gp=0.jpg" alt="" >
				<div>
					<p>{{myName}}</p>
					<span>微信号：Maple_Story<img src='~img/images/my/erweima.png'></span>
				</div>
			</div>
			<img src="~img/images/my/camera.png" alt="" class='camera'>
		</div>
		<ul class='findList'>
			<li @click='showPay=true'>
				<img src="~img/images/my/zhifu.png" alt="">
				<p>支付</p>
			</li>
			<li class='borBottom' @click='showCollection=true'>
				<img src="~img/images/my/shoucang.png" alt="">
				<p>收藏</p>
			</li>
			<li class='borBottom' @click='showAlbum=true'>
				<img src="~img/images/my/xiangce.png" alt="" >
				<p>相册</p>
			</li>
			<li class='borBottom'>
				<img src="~img/images/my/kabao.png" alt="">
				<p>卡包</p>
			</li>
			<li>
				<img src="~img/images/my/biaoqing.png" alt="">
				<p>表情</p>
			</li>
			<li  @click='showSet=true'>
				<img src="~img/images/my/set.png" alt="">
				<p>设置</p>
			</li>
		</ul>
		<van-popup
			  v-model="showMyInfo"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
				:duration=0.2
			>
			<myInfo @isshowMyInfo='isshowMyInfo' ref='myInfo' @cName='changeName'></myInfo>
		</van-popup>
		<van-popup
			  v-model="showPay"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
				:duration=0.2
			>
			<pay @isshowPay='isshowPay' ref='pay'></pay>
		</van-popup>
		<van-popup
			  v-model="showCollection"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
				:duration=0.2
			>
			<myCollection @isshowCollection='isshowCollection' :showCollection='showCollection' ref='myCollection'></myCollection>
		</van-popup>
		<van-popup
			  v-model="showAlbum"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
				:duration=0.2
			>
			<album @isshowAlbum='isshowAlbum' ref='album' v-if='showAlbum'></album>
		</van-popup>
		<van-popup
			  v-model="showSet"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
				:duration=0.2
			>
			<mySet @isshowSet='isshowSet' ref='mySet'></mySet>
		</van-popup>
	</div>
</template>
<script>
	import myInfo from './myInfo'
	import pay from './pay'
	import myCollection from './myCollection'
	import album from './album'
	import mySet from './set'
	export default{
		name:'my',
		data(){
			return{
				showMyInfo:false,
				showPay:false,
				showCollection:false,
				showAlbum:false,
				showSet:false,
				myName:'枫叶回忆'
			}
		},
		mounted(){
			sessionStorage.setItem('myName',this.myName);
		},
		methods:{
			isshowMyInfo(val){
				this.showMyInfo=val;
			},
			isshowPay(val){
				this.showPay=val;
			},
			isshowCollection(val){
				this.showCollection=val;
			},
			isshowAlbum(val){
				this.showAlbum=val;
			},
			isshowSet(val){
				this.showSet=val;
			},
			changeName(value){
				this.myName=value;
			}

		},
		beforeRouteLeave (to, from, next) {
			if(this.$refs.myInfo && this.$refs.myInfo.showImg){
				this.showMyInfo=true;
				this.$refs.myInfo.showImg=false;
				next(false);
		    }	
		    else if(this.$refs.myInfo && this.$refs.myInfo.changeName){
				this.showMyInfo=true;
				this.$refs.myInfo.leaveName();
				next(false);
		    }	
			else if(this.showMyInfo){
				this.showMyInfo=false;
				next(false);
		    }	  
		    else if(this.$refs.pay && this.$refs.pay.showpayManagement){
				this.$refs.pay.showpayManagement=false;
				this.showPay=true;
				next(false)
		    }	     
		    else if(this.showPay){
				this.showPay=false;
				next(false);
		    }	     
		    else if(this.showCollection){
				this.showCollection=false;
				next(false);
		    }	     
		    else if(this.showAlbum){
				this.showAlbum=false;
				next(false);
		    }
		     else if(this.showSet){
				this.showSet=false;
				next(false);
		    }	        	        
		    else{
		    	next();
		    }	
		},
		components:{
			myInfo,pay,myCollection,album,mySet
		}
	}
</script>
<style scoped>
	#my{
		background: #ededed;
		/*padding-bottom: .6rem;*/
		height: 93vh;
	}
	#userInfo{
		padding: .78rem .2rem;
		background: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-top: .9rem;
	}
	.userBox{
		height: 50%;
		width: 92%;
		display: flex;
		align-items: center;
		position: relative;
		margin-top: .4rem;
	}
	.userBox>img{
		width: 1.22rem;
		height: 1.22rem;
		border-radius: .16rem;
	}
	.userBox>div{
		margin-left: .3rem;
		display: flex;
		width: 100%;
		flex-direction: column;
	}
	.userBox>div>p{
		font-size: .4rem;
		margin-bottom: .1rem;
		font-weight: 600;
		width: 81%;
	}
	.userBox>div>span{
		font-size: .33rem;
		color: #7f7f7f;
		background: #fff url('~img/images/right.png') no-repeat right center/.3rem;
		display: flex;
		align-items: center;
	}
	.userBox>div>span>img{
		width: .36rem;
	    height: .36rem;
	    position: absolute;
	    right: .6rem;

	}
	.camera{
		position: absolute;
	    top: .27rem;
	    right: .4rem;
	    width: .5rem;
	}
	.findList{
		margin-top: .2rem;	
		margin-bottom: 1.9rem;	
	}
	.findList>li{
		background: #fff;	
		padding:.3rem;
		padding-right: 0;
		display: flex;
		align-items: center;
		background: #fff url('~img/images/right.png') no-repeat 96% center/.3rem;
	}
	.findList>li>img{
		width: .52rem;
    	height: .52rem;
	}
	.findList>li>p{
		font-size: .38rem;
    	margin-left: .2rem;
    	
	}
	.findList>li:nth-of-type(1),.findList>li:nth-of-type(5){
		margin-bottom: .2rem;		
	}
	.borBottom p:after ,.borBottom p:after,.borBottom p:after{
		content: '';
		display: block;
		width: 86vw;
		position: relative;
		top:.3rem;
	    border-bottom:1px solid rgba(233,233,233,.6);
	}
	.van-image-preview {
		background: rgba(0,0,0,.8);
	}
</style>


