<template>
	<div id="pay">
		<similarHeader title='支付' @goBack='goBack' @goMore='goMore' :havaMore='true'>
			<!-- <h1 slot='demo' @click='al'>123</h1> -->
			<!-- <template slot='goBack'>
				<img src="~img/images/chat/left.png" alt="" class='goBack' @click='goBack'>
			</template> -->
			<!-- <template slot='title'>
				支付
			</template>
			<template slot='more'>
				<img src="~img/images/chat/more.png" alt="" class='more' @click='showpayManagement=true'>
			</template> -->
		</similarHeader>
		<van-popup
			  v-model="showpayManagement"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
			>
				<payManagement @isShowPop='isShowPop'></payManagement>
				<!-- 支付管理 -->
			</van-popup>
		<ul class='payTop'>
			<li>
				<img src="~img/images/pay/money.png" alt="">
				<span>收付款</span>
			</li>
			<li>
				<img src="~img/images/pay/wallet.png" alt="" class="wallet">
				<span>钱包</span>	
				<span class=money>￥18888888</span>
			</li>
		</ul>
		<div class="payBox">
			<h2>腾讯服务</h2>
			<ul>
				<li>
					<img src="~img/images/pay/chong.png" alt="">
					<span>手机充值</span>	
				</li>
				<li>
					<img src="~img/images/pay/payment.png" alt="">
					<span>生活缴费</span>	
				</li>
				<li class='transparent'>
					<img src="~img/images/pay/cityService.png" alt="">
					<span>城市服务</span>	
				</li>
			</ul>
		</div>
		<div class="payBox">
			<h2>第三方服务</h2>
			<ul>
				<li class='borderBottom'>
					<img src="~img/images/pay/didi.png" alt="">
					<span>滴滴出行</span>	
				</li>
				<li class='borderBottom'>
					<img src="~img/images/pay/waimai.jpg" alt="">
					<span>美团外卖</span>	
				</li>
				<li class='transparent borderBottom'>
					<img src="~img/images/pay/cinema.png" alt="">
					<span>电影演出赛事</span>	
				</li>
				<li>
					<img src="~img/images/pay/train.png" alt="">
					<span>火车票机票</span>	
				</li>
				<li>
					<img src="~img/images/pay/eatPlay.png" alt="">
					<span>吃喝玩乐</span>	
				</li>
				<li class='transparent'>
					<img src="~img/images/pay/pingduoduo.jpg" alt="">
					<span>拼多多</span>	
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import payManagement from './pay-management'
	import  similarHeader from './similar-header'
	import {mixinTest1} from '@/mixin'
	export default{
		// mixins:[mixinTest1],
		name:'pay',
		data(){
			return{
				showpayManagement:false
			}
		},
		methods:{
			isShowPop(val){
				this.showpayManagement=val;
			},
			goBack(){
				this.$emit('isshowPay',false)
			},
			goMore(){
				this.showpayManagement=true;
			}
		},
		components:{
			payManagement,similarHeader
		}
	}
</script>
<style scoped>
	#pay{
		background: #f7f7f7;
		overflow:auto;
		height: 100%;
	}
	header.similar{
		background: #f7f7f7;
	}
	.payTop{
		background: #3bac6a;
		width: 94vw;
		display: flex;
		margin:.2rem auto;
		margin-top: .28rem;
		align-items: center;
		justify-content: center;
		border-radius: .18rem;
	}
	.payTop li{
		display: flex;
		width: 50%;
		align-items: center;
		flex-direction: column;
		padding:.62rem .7rem;
	}
	.payTop img{
		width: 1rem;
		margin-bottom: .24rem;
		height: 1rem;
	}
	.payTop img.wallet{
		width: .9rem;
		height: .9rem;
		margin-top: .08rem;
	}
	.payTop span{
		font-size: .4rem;
		color:white;
	}
	.payTop li:last-child{
		position: relative;
	}
	.payTop span.money{
		color: #8bdaad;
		margin-top: .12rem;
		position: absolute;
		bottom: .1rem;
	    left: 50%;
	    font-weight: 600;
	    transform: translateX(-50%);
	}
	.payBox{
		background: #fff;
	    width: 94vw;
	    margin: .2rem auto;
	    border-radius: .18rem;
	}
	.payBox h2{
		font-size: .36rem;
	    color: #7c7c7c;
	    font-weight: 400;
	    padding: .3rem .4rem;
    	border-bottom: 1px solid #ececec;
	}
	.payBox img{
		width: .7rem;
		height: .7rem;
		margin-bottom: .2rem;
	}
	.payBox ul{
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
	}
	.payBox ul li {
		width: 33%;
	    font-size: .32rem;
	    display: flex;
	    box-sizing: border-box;
	    border-right: 1px solid #ececec;
	    flex-direction: column;
	    padding: .4rem;
	    align-items: center;
	    white-space: nowrap;
	    color:#616161;
	}
	ul li.transparent{
		border-right: 1px solid transparent;
	}
	ul li.borderBottom{
		border-bottom: 1px solid #ececec;
	}
	.payBox:last-child{
		margin-bottom: .25rem !important;
	}
</style>


