<template>	
	<div id="set">	
		<similarHeader title='设置' @goBack='goBack' :havaMore='false'></similarHeader>
		<ul class='list'>
			<li class='borderBottom'>
				账号与安全
			</li>
			<li>
				新消息提醒
			</li>
			<li>
				勿扰模式
			</li>
			<li>
				聊天
			</li>
			<li>
				隐私
			</li>
			<li  class='borderBottom'>
				通用
				
			</li>
			<li>
				关于微信
			</li>
			<li  class='borderBottom'>
				帮助与反馈
			</li>
			<li  class='borderBottom'>
