</template>
<script>	
	import  similarHeader from './similar-header'
	export default{
		data(){
			return{

			}
		},
		methods:{
			goBack(){
				this.$emit('isshowSet',false)
			}
		},
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	#set{
		background: #ededed;
		overflow: hidden;
	}
	.list{
		background: #fff;
		/*margin-top: .84rem;*/
	}
	.list li {
		font-size: .38rem;
	    padding: .3rem 0;
	    position: relative;
	    margin-left: .3rem;
	    display: flex;
	    align-items: center;
	    background: #fff url('~img/images/right.png') no-repeat 96% center/.3rem;
	    border-bottom:1px solid #ededed;
	}
	.list li.borderBottom{
		border-bottom: .2rem solid #ededed;
		margin-left: 0;
		padding-left: .3rem;
	}
	li.center{
		justify-content: center;
	}
	.list li:last-child{
		margin-bottom: .6rem;
	}
</style>


